const form = document.querySelector('form');
const firstDelayImput = document.querySelector('input[name="delay"]');
const delayStepImput = document.querySelector('input[name="step"]');
const amountImput = document.querySelector('input[name="amount"]');
const btn = document.querySelector('button');

let amount;
let delay;
let step;

form.addEventListener('submit', onFormSubmit);
function onFormSubmit(evt) {
  evt.preventDefault();
  amount = parseInt(amountImput.value);
  delay = parseInt(firstDelayImput.value);
  step = parseInt(delayStepImput.value);

  for (let i = 0; i < amount; i += 1) {
   const position = i + 1;

      function createPromise(position, delay) {
      return new Promise((resolve, reject) => {
    setTimeout(() => {
const shouldResolve = Math.random() > 0.3;
  if (shouldResolve) {
     resolve({ position, delay });
  } else {
    reject({ position, delay });
  }
  }, delay);
      });
    };

    createPromise(position, delay)
  .then(({ position, delay }) => {
    console.log(`✅ Fulfilled promise ${position} in ${delay}ms`);
  })
  .catch(({ position, delay }) => {
    console.log(`❌ Rejected promise ${position} in ${delay}ms`);
  });
     delay += step;
  };
};




